<template>
  <div id="app">
    <Toasts></Toasts>
    <button
      class="admin-btn"
      @click="changeAdminNav"
      title=" "
      :disabled="!isAdmin"
    >
      <span v-if="!adminNav"></span>
      <span v-else></span>
    </button>
    <div id="nav" :class="{ 'admin-show': adminNav, 'admin-hide': !adminNav }">
      <router-link to="/"></router-link>|
      <router-link to="/choose"> </router-link>|
      <router-link to="/main"> </router-link>|
      <router-link to="/about"> </router-link>
    </div>
    <div id="view" :class="{ 'full-screen': !adminNav }">
      <transition name="slide" mode="out-in" appear>
        <router-view></router-view>
      </transition>
    </div>
  </div>
</template>
<script>
import jwt from "jsonwebtoken";
export default {
  data() {
    return {
      user: "",
      message: "",
      messages: []
    };
  },
  sockets: {
    connect: function(connections) {
      this.$store.state.socketId = this.$socket.id;
      console.log(this.$socket.id);
      console.log("Socket connected");
      let token = this.$store.state.token;
      this.$socket.emit("authenticate", { token });
    },
    authenticated: function() {
      console.log("!");
    },
    unauthorized: function(error) {
      console.error(" !");
      if (
        error.data.type === "UnauthorizedError" ||
        error.data.code === "invalid_token"
      ) {
        // TODO:     ,   
        console.error(" ");
        // this.$router.push("Home");
      }
    },
    setToast: function(toast) {
      switch (toast.type) {
        case "success":
          this.$toast.success(toast.body, {
            showProgress: true,
            timeOut: toast.timeOut
          });
          break;
        case "warning":
          this.$toast.warning(toast.body, {
            showProgress: true,
            timeOut: toast.timeOut
          });
          break;
        case "danger":
          this.$toast.error(toast.body, {
            showProgress: true,
            timeOut: toast.timeOut
          });
          break;
        case "info":
        default:
          this.$toast.info(toast.body, {
            showProgress: true,
            timeOut: toast.timeOut
          });
          break;
      }
    }
  },
  beforeCreate() {
    let decode = jwt.decode(this.$store.state.token);
    if (decode) {
      if (decode.admin) {
        this.$store.commit("SET_USER_ID", decode.id);
        this.$store.commit("changeAdminStatus");
      }
      let name = decode.name;
      // this.$socket.emit("setName", name);
      this.$store.commit("SET_NAME", name);
      // console.log("setStateFromLS");
      // this.$store.commit("setStateFromLS");
      if (this.$store.state.gamerName) {
        this.$socket.emit("setName", this.$store.state.gamerName);
      }
      if (this.$store.state.roomId) {
        this.$socket.emit("setRoom", this.$store.state.roomId);
      }
      console.log("SEND RESRESH!");
      this.$socket.emit(
        "refreshSocketID",
        this.$store.state.socketId,
        this.$store.state.roomId
      );
    }
  },
  methods: {
    changeAdminNav() {
      this.$store.commit("changeAdminNav");
    }
  },
  computed: {
    adminNav() {
      return this.$store.state.adminNav;
    },
    isAdmin() {
      return this.$store.state.isAdmin;
    }
  },
  mounted() {
    const token = localStorage.getItem("user-token");
    if (token) {
      this.$http.defaults.headers.common["Authorization"] = token;
    }
    this.$http.interceptors.response.use(undefined, function(err) {
      return new Promise(function() {
        if (err.response) {
          console.log(err.response);
        }
        if (err.status === 401 && err.config && !err.config.__isRetryRequest) {
          //     ,  
          this.$store.dispatch("AUTH_LOGOUT");
          this.$router.push("/login");
        }
        throw err;
      });
    });
  }
};
</script>
<style>
body {
  overflow-x: hidden;
  overflow-y: hidden;
  padding: 0;
  margin: 0;
  height: 100vh;
}
#app {
  height: 100%;
  position: relative;
}
#nav {
  box-sizing: border-box;
  padding: 8px;
  height: 40px;

  width: 100%;
}
#view {
  height: calc(100vh - 40px);
}
.full-screen {
  height: calc(100vh - 00px) !important;
  max-height: calc(100vh - 00px) !important;
}
.slide-enter-active,
.slide-leave-active {
  transition: all 0.2s;
}

.slide-leave-active {
  transform: translateX(-110%);
}
.slide-enter {
  transform: translateX(110%);
}
.admin-btn {
  color: #fff;
  background-color: #007bff;
  border: none;
  position: fixed;
  top: 0;
  right: 0;
  height: 40px;
  box-sizing: border-box;
  padding: 5px 10px;
  font-size: 1.4rem;
  z-index: 1000;
}
.admin-btn[disabled] {
  background-color: #6bb3ff;
  visibility: hidden;
}
.admin-show {
  position: relative;
}
.admin-hide {
  position: static;
  position: fixed;
  top: -36px;
  left: 0;
}
.loader-wrap {
  width: 100%;
  display: flex;
}
.loader-wrap > * {
  margin: auto;
}
@media (max-width: 930px) {
  .toast-container {
    right: 1rem !important;
    width: calc(100vw - 2rem) !important;
    min-width: calc(100vw - 2rem) !important;
  }
  .toast {
    max-width: calc(100vw - 2rem) !important;
  }
}
</style>
